.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 2rem 6rem;
}

.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__navbar-links-logo {
  margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
  width: 62.56px;
}

.gpt3__navbar-links_container {
  display: flex;
  flex-direction: row;
  margin-left: 50px;
}
.gpt3__navbar-sign {
  display: flex;
  justify-content: flex;
  align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
  color: rgb(255, 255, 255);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;

  margin: 0 1rem;
  cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
  padding: 0.5rem 1rem;
  color: #fff;
  background: /* ff 3.6+ */ -moz-linear-gradient(
    301deg,
    rgba(38, 151, 158, 1) 0%,
    rgba(51, 98, 152, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(
    301deg,
    rgba(38, 151, 158, 1) 0%,
    rgba(51, 98, 152, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-linear-gradient(
    301deg,
    rgba(38, 151, 158, 1) 0%,
    rgba(51, 98, 152, 1) 100%
  );

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#26979E', endColorstr='#336298', GradientType=0 );

  /* ie 10+ */
  background: -ms-linear-gradient(
    301deg,
    rgba(38, 151, 158, 1) 0%,
    rgba(51, 98, 152, 1) 100%
  );

  /* global 94%+ browsers support */
  background: linear-gradient(
    301deg,
    rgba(38, 151, 158, 1) 0%,
    rgba(51, 98, 152, 1) 100%
  );
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
}
.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.gpt3__navbar-menu svg {
  cursor: pointer;
}

.gpt3__navbar-menu_container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  color: white;

  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;

  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu_container p {
  margin: 1rem 0;
  color: rgb(255, 255, 255);
}
.a-tag {
  color: rgb(255, 255, 255);
}
.gpt3__navbar-menu_container-links-sign {
  display: none;
}

@media screen and (max-width: 1050px) {
  .gpt3__navbar-links_container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
}
@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }

  .gpt3__navbar-sign {
    display: none;
  }
  .gpt3__navbar-menu_container {
    top: 20px;
  }
  .gpt3__navbar-menu_container-links-sign {
    display: block;
  }
}
